import React, { useState } from "react";
import Card from '../UI/Card';
import Button from "../UI/Button";
import classes from './AddUser.module.css';
import ErrorModal from "../UI/ErrorModal";


const AddUser = (props) => {

const [enteredUsername, setEnteredUsername] = useState('');
const [enteredAge, setEnteredAge] = useState('');
const [error, setError] = useState();
 
const usernameChangeHandler = (event) => {
setEnteredUsername(event.target.value);
};
 
const ageChangeHandler = (event) => {
    setEnteredAge(event.target.value);
    };
 
    const addUserHandler = (event) => {
        event.preventDefault();
       if (enteredUsername.trim().length === 0 || enteredAge.trim().lenght === 0) {
           setError({
               title: 'Invalid input',
               message: 'Please enter a valid fucking name and age(non-empty values.)'
           });
           return;
       } if (+enteredAge < 1) {
        setError({
            title: 'Invalid age',
            message: 'Please enter a valid fucking age (> 0)'
        });
           return;
       }
        props.onAddUser(enteredUsername, enteredAge);
        setEnteredUsername('');
        setEnteredAge('');
    }

const errorHandler = () => {
    setError(null);
}

return (
    <div>
   {error && <ErrorModal title={error.title} message={error.message} onConfirm={errorHandler} />}
    <Card className={classes.input}>
<form onSubmit={addUserHandler}>
<label htmlFor="username">Username</label>
<input id="username" type="text" value={enteredUsername} onChange={usernameChangeHandler} />
<label>Age(Years)</label>
<input htmlFor="age" type="number" value={enteredAge} onChange={ageChangeHandler} />
<Button type="submit">Add user</Button> </form>
</Card>
</div>
);

};

export default AddUser;